interface EventCardProps {
  title: string
  description: string
  date: string
  category: string
  delay: string
}

function EventCard({ title, description, date, category, delay }: EventCardProps) {
  const [isHovered, setIsHovered] = useState(false)

  return (
    <div 
      className="relative bg-white rounded-2xl shadow-lg hover:shadow-xl transition-all duration-300 hover:-translate-y-2 overflow-hidden cursor-pointer"
      data-aos="fade-up" 
      data-aos-delay={delay}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
    >
      <div className="h-48 bg-gradient-to-br from-blue-100 to-purple-100 flex items-center justify-center relative">
        <div className="text-6xl font-bold text-blue-600/20">
          {title.split(' ').map(word => word[0]).join('').slice(0, 3)}
        </div>
        <div className="absolute top-4 left-4 bg-blue-600 text-white px-3 py-1 rounded-full text-sm font-semibold">
          {category}
        </div>
      </div>
      
      <div className={`absolute inset-0 bg-gradient-to-t from-black/80 via-transparent to-transparent transition-opacity duration-300 ${isHovered ? 'opacity-100' : 'opacity-0'}`}>
        <div className="absolute bottom-0 left-0 right-0 p-6 text-white transform transition-transform duration-300" style={{ transform: isHovered ? 'translateY(0)' : 'translateY(100%)' }}>
          <h3 className="text-xl font-bold mb-2">{title}</h3>
          <p className="text-sm opacity-90 mb-2">{description}</p>
          <p className="text-xs opacity-75">{date}</p>
        </div>
      </div>
    </div>
  )
}

